.list {
  display: grid;
  max-width: calc(90vw - 20px);
  grid-template-columns: repeat(auto-fill, minmax(130px, 1fr));
  grid-gap: 16px;
  margin-top: 0;
  margin-bottom: 0;
  padding: 0;
  list-style: none;
  margin-left: auto;
  margin-right: auto;
}

.list__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  border-radius: 2px;
  box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2),
    0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 2px 1px -1px rgba(0, 0, 0, 0.12);
}

.list__item > p {
  font-weight: 600;
}

.list__img {
  width: 100%;
  height: auto;
  object-fit: cover;
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.list__noimg {
  width: 100%;
  height: 100%;
  position: relative;
  left: 30px;
  top: 70px;
}

@media (min-width: 600px) {
  .list {
    width: 100%;
    max-width: 100%;
    padding: 0 20px; /* Dodatkowe marginesy po bokach */
    grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
    grid-gap: 20px;
  }

  .list__item {
    align-items: flex-start;
  }

  .list__img {
    width: 100%;
  }
}
